import React, {useState} from 'react'
import Button from 'react-bootstrap/Button';

export default function Bfa() { 
    const [data,setData]=useState(null)
    const [print,setPrint]=useState(false)
    function getData(val){
        setData(val.target.value)
        setPrint(false)
        console.write(val.target.value)         
    }

return (
    <body>
        <br/>
        <h2>Bfa feladat ellenrzse</h2>
        <br/>
        <label>rd be a fokszmot(t):
        <input type="text" onChange={getData}/>   
        <Button onClick={()=>setPrint(true)} variant='primary'>Bead</Button> 
        </label>              
        <br/>
        <div>
            <br/>
            {
            print?
            <p><pre>Fokszm: {data}<br/><br/>Cscs maximum rtke:{2*data-1}     Gyerek maximum rtke:{2*data}<br/>
            Cscs minimum rtke:{data-1}     Gyerek minimum rtke:{1*data}</pre></p>          
            :null                
            }
            <br/><br/>
            
            <div className='bfaaddon'>
                <label>
                Elemek beadsa:
                <input type="text"/>
                <Button variant='primary'>Megads</Button>
                <br/>
                </label>                
            </div>             
             
            <div className='bfarajz'>
                <a>dsad</a>
            </div>

        </div>
    </body>    
    )
}
